.utility_space{
    letter-spacing: 2px;
    text-transform: uppercase;
}
.content{
    color:black;
    text-align: center;
    position: relative;
}
.table{
    margin-top: 10px;
    display: flex;
    color:black;
}
.table-left{
    width: 50%;
}
.table-right{
    width: 50%;
}
.heading-h1{
    font-size: 2.3rem;
    color:#189AB4;
}
.heading-h2{
    color:#05445E;
    margin:5px 0;
}
.content_para{
    font-weight: 600;
    color: rgba(0,0,0,0.8);
    margin:5px 0;
    letter-spacing: 1px;
    text-transform: uppercase;
}
.table_title{
    font-size: 1.4rem;
    color:#f55c47;
}
.table_time{
    color:rgba(0,0,0,0.7);
}
li{
    list-style-type:circle;
    font-weight: 600;
    color:#05445E;
    margin-left: 20px;
}
.button_div{
    margin:10px 0;
    margin-top: 30px;
    display: flex;
    justify-content: center;
}

/* course detail 2 */
.list-item{
    text-transform: capitalize;
    letter-spacing: 1px;
    line-height:2;
    text-align: left;
    font-weight: bold;
}
.list-item--utility{
    list-style-type:none;
}
.courseDetail2-container{
display: flex;
margin-top: 10px;
}
.courseDetail2-img{
    width: 300px;
    height: 200px;
}
.courseDetail2-img--div{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.coming-soon-div{
    width: 60%;
    padding: 30px 0;
}
.coming-soon--img{
    width: 260px;
}
.ul-courseDetail2{
    margin-left: 10px;
}
.new-batch{
    text-transform: uppercase;
    letter-spacing: 2px;
    color:#f55c47;
}
.new-batch--div{
    padding:5px;
    margin: 20px 0;
}
.new_batch--img{
    width: 150px;
    border-radius: 50%;
    position: absolute;
    top:0;
    right:0;
}
.new-batch-course{
    display: flex;
    align-items: center;
    margin: 0 60px 0 0px;
}

@media screen and (max-width:990px) {
    .heading-h1{
        font-size: 2.0rem;
    }
    .heading-h2{
        font-size: 1.4rem;
    }
    .content_para{
        font-size: 0.9rem;
    }
    .table_title{
        font-size: 1.2rem;
    }
    .button_div{
        margin:5px 0;
        margin-top: 20px;
        display: flex;
        justify-content: center;
    }
    .table_time{
        font-size: 1rem;
    }

    .courseDetail2-img{
        width: 250px;
        height: 160px;
    }
    
}
@media screen and (max-width:860px) {
    .table{
        display: flex;
        flex-direction: column;
    }
    .table-left{
        width: 100%;
        margin: 10px 0;
    }
    .table-right{
        width: 100%;
    }
    .courseDetail2-img{
        width: 200px;
        height: 120px;
    }
    .list-item{
        font-size: .8rem;
    }
    .coming-soon--img{
        width: 200px;
    }
    .new-batch-course{
        margin: 0px;
        font-size: 0.9rem;
        margin: 10px 0 30px 0;
    }
    .newbatch-button{
       display: flex;
       flex-direction: column;
    }
    
}
@media screen and (max-width:600px) {
    .courseDetail2-container{
        display: flex;
        flex-direction: column;
        margin: 30px 0;
        }
        .courseDetail2-img--div{
            width: 100%;
        }
        .ul-courseDetail2{
            margin: 20px 0;
        }
        .heading-h1{
            font-size: 1.5rem;
            margin-top: 30px;
        }
        .courseDetail2-img{
            width: 250px;
            height: 160px;
        }
        .coming-soon--img{
            width: 250px;
            margin: 30px 0;
        }
        .table-left{
            width: 100%;
            margin: 30px 0;
        }
        .table-right{
            width: 100%;
        }

}
@media screen and (max-width:500px) {
    .heading-h1{
        font-size: 1.5rem;
    }
    .heading-h2{
        font-size: 1.1rem;
    }
    .content_para{
        font-size: 0.7rem;
    }
    .table_title{
        font-size: 1.1rem;
    }
    .button_div{
        margin:5px 0;
        margin-top: 20px;
        display: flex;
        justify-content: center;
    }
    .table_time{
        font-size: 1rem;
    }
    
}