.courses_section{
    width: 100%;
    height: auto;
    background-color:#189AB4;
    color: #f7f7f7;
    padding: 100px 0;
}
.utility__bottom-border{
  border-bottom: 5px solid #f7f7f7;
  }
.courses__card--container{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.courses_card{
width: 300px;
background-color: #05445E;
color:#f7f7f7;
border-radius: 6px;
box-shadow: 0 1.5rem 4rem rgba(0, 0,0,0.1);
transition: all 0.5s ease;
margin:20px 50px;
position: relative;
cursor: pointer;
}
.courses_card:hover{
    transform: translateY(-25px);
}
.courses_card img{
    width: 100%;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}
.course_text{
text-align: center;
padding: 5px 30px;
margin-bottom:20px;
opacity: 0.9;

}
.course_name{
text-transform: uppercase;
letter-spacing: 1px;
}
.course_descrip{
 font-weight: 500;
 margin: 10px 0;
 opacity: .9;
 font-size: 0.8rem;
}
.course_price{
font-weight: bold;
font-size: 1.3rem;
}
.course_card_hr{
    border: none;
    border-top: 1px dashed #f7f7f7;
}
