.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 100;
  }
  .modal-main {
    position:fixed;
    background: white;
    width: 100%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
  }
  .cancel-button{
      position: absolute;
      top: 0;
      right: 0;
      z-index: 101;
      width: 50px;
      height: 50px; 
      border-radius: 50%;
      padding: 2px;
      font-size: 3rem;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      right: 20px;
      top:20px;
      border: none;
      cursor: pointer;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
 @media screen and (max-width:600px) {
     .cancel-button{
        width: 40px;
        height: 40px; 
     }
 }
  @media  (max-width:400px) {
    .course_name{
        font-size: 1rem;
        }
        .course_descrip{
         font-size: 0.7rem;
        }
        .course_price{
        font-size: 1.1rem;
        }
        .courses_section{
            padding: 50px 0;
        }
  }