.tuition-section-one{ 
    text-transform: uppercase;
    padding:15px 200px;
    letter-spacing: 3px;
    display: flex;
    justify-content: flex-start;
    position: relative;
}
.tuition-section{
    border-bottom: 4px solid #189AB4;
}
.color-one{
    background-color: #189AB4;
    color:#f7f7f7;
}
.color-two{
    background-color: #f7f7f7;
    color: #189AB4;
}
.tuition-section--button{
    font-weight: bold;
    font-size: 1.2rem;
    padding:10px 15px;
    border:none;
    border-radius: 50px;
    cursor: pointer;
    position: absolute;
    right:150px;
}
.tuitions-heading{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
}
@media screen and (max-width:1300px) {
    .tuition-section-one{ 
        padding:15px 200px;
        letter-spacing: 3px;
    }
    .tuition-section-one h1{
        font-size: 1.6rem;
    }
    .tuition-section--button{
        font-size: 1.0rem;
        padding:10px 15px;
        right:150px;
    }
}
@media screen and (max-width:1200px) {
    .tuition-section-one h1{
        font-size: 1.3rem;
    }
    .tuition-section--button{
        font-size: 1.0rem;
        padding:8px 10px;
        right:100px;
    }
}
@media screen and (max-width:850px) {
    .tuition-section-one{ 
        padding:15px 100px 15px 30px;
    }
    .tuition-section-one h1{
        font-size: 1.3rem;
    }
    .tuition-section--button{
        font-size: 1.0rem;
        padding:8px 10px;
        right:50px;
    }
}
@media screen and (max-width:450px) {
    .tuition-section-one{ 
        padding:15px 100px 15px 20px;
    }
    .tuition-section-one h1{
        font-size: 1.0rem;
    }
    .tuition-section--button{
        font-size: 1.0rem;
        padding:8px 10px;
        right:30px;
    }
}
@media screen and (max-width:370px) {
 .tuition-section--heading{
     font-size: 1.4rem;
 }
}