body{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.utility_center_text{
    text-align: center;
  }
  .about_section{
    background-color: #f7f7f7;
    color:rgba(46, 49, 65,1);
    padding:100px 0;
    padding-bottom: 200px;
  }
  .about_section__heading{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 40px;
  letter-spacing: 3px;
  border-bottom: 5px solid rgba(46, 49, 65,1);
  display: inline-block;
  }
  .utility_bottom_margin{
    margin-bottom:100px;
  }
  .about_section__row{
    display: flex;
    width: 70%;
    margin: 0 auto;
  }
  .about_section__col_one{
    width: 50%;
    padding-right: 100px;
  }
  .about_section__col_two{
    width: 50%;
  }
  .about_section__paragraph{
    font-weight: 500;
    text-align: left;
  }
  .composition{
    position: relative;
  }

  .composition_photo{
    box-shadow: 0 1.5rem 4rem rgba(0,0,0,.4);
    border-radius: 4px;
    width: 55%;
    position: absolute;
    z-index: 10;
    transition: all .2s;
    outline-offset: 15px;
  }
  .composition_photo:hover{
    transform: scale(1.05) translate(-5px);
    box-shadow: 0 2.5rem 4rem rgba(0,0,0,.6);
    z-index: 20;
    outline: 10px solid #189AB4;
  }
  
  .composition_photo_p1{
    left: 0;
    top: -20px;
  }
  
  .composition_photo_p2{
    right: 70px;
    top: 20px;
  }
  .composition_photo_p3{
    left: 15%;
    top: 90px;
  }
  .composition:hover .composition_photo:not(:hover){
    transform: scale(.9);
  }
 .about_section_tagline{
    display: flex;
    justify-content: flex-start;
    padding-left: 20px;
  }
  .about_section_tagline-text{
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 10px 5px;
  font-size: 1.6rem;
  }
  .about_section_tagline-think-text{
    color:#FFAEBC;
    border-bottom: 4px solid #FFAEBC;
    text-shadow: 0 0 3px #FFAEBC, 0 0 2px #000000;
  }
  .about_section_tagline-design-text{
    color:#A0E7E5;
    border-bottom: 4px solid #A0E7E5;
    text-shadow: 0 0 3px #A0E7E5, 0 0 2px #000000;
  
  }
  .about_section_tagline-develop-text{
    color: #B4F8C8;
    border-bottom: 4px solid #B4F8C8;
    text-shadow: 0 0 3px #B4F8C8, 0 0 2px #000000;
  
  
  }
  @media (max-width:1250px){
    .composition{
      top:30%;
      left:50%;
      transform: translate(-50%,-30%);
    }
    .about_section_tagline{
      padding-left: 0px;
    }
  }
  @media screen and (max-width:1000px) {
    .about_section__row{
      width: 90%;
    }
    .composition_photo:hover{
      filter: blur(0px);
    }
    .about_section_tagline-text{
      text-transform: uppercase;
      letter-spacing: 2px;
      margin: 10px 5px;
      font-size: 1.4rem;
      }
  }
  @media screen and (max-width:780px) {
    .about_section_tagline-text{
      margin: 10px 3px;
      font-size: 1.2rem;
      }
  }
  @media screen and (max-width:600px) {
    .about_section{
      padding-bottom: 60px;
    }
    .about_section__row{
      flex-direction: column-reverse;
      align-items: center;
    }
    .about_section__col_one{
      width: 80%;
      padding-right: 0px;
    }
    .about_section__paragraph{
      text-align: justify;
    }
    .about_section__col_two{
      width: 80%;
      margin: 40px 0;
      height: 30vh;
    }
    .about_section__heading{
      margin-bottom: 30px;
      }
      .composition_photo_p1{
        left: 50px;
        right: -20px;
      }
      .composition_photo_p2{
        right: 30px;
        top: 20px;
      }
      .composition_photo_p3{
        left:25%;
        top: 90px;
      }
      .about_section_tagline{
        justify-content: center;
      }
      .utility_bottom_margin{
         margin: 0px;
      }
  }
  @media screen and (max-width:400px) {
    .about_section{
      padding-top:30px ;
    }
    .composition_photo{
      width: 70%;
    }
    .composition_photo_p1{
      left: 10px;
      right: -20px;
    }
    .composition_photo_p2{
      right: 0px;
      top: 20px;
    }
    .composition_photo_p3{
      left:10%;
      top: 70px;
    }
    .about_section__heading{
      font-size: 1.8rem;
    }
    .about_section__heading{

    }
    .about_section__col_two{

    }
    .about_section__paragraph{

    }
    .composition{
      position: relative;
      top:30px
    }
  }